import './AdminPedidos.css'

import {
  IonAlert,
  IonContent,
  IonDatetime,
  IonHeader,
  IonItem,
  IonLabel,
  IonList,
  IonModal,
  IonTitle,
  IonToolbar,
} from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'
import Select from 'react-select'

import { Page, SDataTable } from '../../components'
import LoaderAdmin from '../../components/LoaderAdmin'
import { RUTAS } from '../../constants'
import { useApiToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Factura } from '../../services/api'

const moment = require('moment-timezone')
const numeral = require('numeral')

export const AdminPagoCreate: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const DataTable = React.createRef<any>()
  const [Tipo, setTipo] = useState<any>({ value: 10, label: 'Proveedor' })
  const [PersonaRendir, setPersonaRendir] = useState<any>()
  const [Fabricantes, setFabricantes] = useState<any[]>([])
  const [Transportistas, setTransportistas] = useState<any[]>([])
  const [Data, setData] = useState<any[]>([])
  const [ModalGenerarFactura, setModalGenerarFactura] = useState(false)
  const [HabilitarGenerarFactura, setHabilitarGenerarFactura] = useState(false)
  const [showAlert, setShowAlert] = useState(false)
  const [MontoFacturar, setMontoFacturar] = useState<number>(0)
  const [Ready, setReady] = useState(false)
  // Datos para generar el pago
  const [MontoPago, setMontoPago] = useState<any>(0)
  const [FilasSeleccionadas, setFilasSeleccionadas] = useState<any[]>([])
  const [TipoId, setTipoId] = useState<number>(10)
  const [PersonaRendirId, setPersonaRendirId] = useState<any>()
  const [FechaFactura, setFechaFactura] = useState('')
  const [Loading, setLoading] = useState(false)
  const [Factura, setFactura] = useState<Factura | null>()
  const DataPrueba = useRef<any>()
  const [FundAccounts, setFundAccounts] = useState<any[]>([])
  const [SelectedFundAccount, setSelectedFundAccount] = useState<number>()
  const apiToast = useApiToaster()

  const setup = () => {
    obtenerFabricantes()
    obtenerTransportistas()
    obtenerCuentasDeFondos()
    setReady(true)
    if (history.location.hasOwnProperty('factura')) {
      // @ts-ignore
      precargarPago(history.location.factura)
    } else {
      setTipo({ value: 10, label: 'Fabricante' })
      setTipoId(10)
      setPersonaRendirId(null)
      setFactura(null)
    }
  }

  React.useEffect(() => {
    setup()
  }, [])

  const precargarPago = (factura: Factura) => {
    setLoading(true)
    setFactura(factura)
    setTimeout(() => {
      if (factura.tipo === 10) {
        setTipo({ value: 10, label: 'Fabricante' })
      } else {
        setTipo({ value: 20, label: 'Transportista' })
      }
      setTipoId(factura.tipo)
    }, 100)
    setTimeout(() => {
      const persona = DataPrueba.current.filter((val: any) => {
        return val.value === factura.persona_id
      })
      setPersonaRendir(persona[0])
      setPersonaRendirId(factura.persona_id)
      setLoading(false)
    }, 500)
  }

  const obtenerFabricantes = () => {
    rootStore.environment.api.fabricantesList().then((result) => {
      if (result.kind === 'ok') {
        const data = result.data.map((dat: any) => {
          return { value: dat.id, label: dat.nombre }
        })
        DataPrueba.current = data
        setFabricantes(data)
        setData(data)
      }
    })
  }

  const obtenerCuentasDeFondos = () => {
    rootStore.environment.api.getFundAccounts().then((result) => {
      if (result.kind === 'ok') {
        const data = result.data.map((dat: any) => {
          return { value: dat.id, label: dat.name }
        })
        setFundAccounts(data)
      }
    })
  }

  const obtenerTransportistas = () => {
    rootStore.environment.api.transportistaList().then((result) => {
      if (result.kind === 'ok') {
        const data = result.data.map((dat: any) => {
          return { value: dat.id, label: dat.nombre }
        })
        setTransportistas(data)
        DataPrueba.current = data
      }
    })
  }

  const registrarPago = () => {
    rootStore.environment.api
      .registrarPagoProveedor(SelectedFundAccount, MontoPago, FilasSeleccionadas, TipoId, PersonaRendirId, FechaFactura)
      .then((result) => {
        apiToast(result, 'Pago registrado correctamente', 'registrar el pago')
        if (result.kind === 'ok') {
          setModalGenerarFactura(false)
        }
      })
  }

  useEffect(() => {
    if (Ready && !Loading && DataTable) {
      if (Tipo && Tipo.value === 10) {
        setData(Fabricantes)
      } else {
        setData(Transportistas)
      }
      setPersonaRendir(null)
      setPersonaRendirId(null)
      DataTable.current.refresh()
      DataTable.current.clearSelected()
    }
  }, [Tipo])

  useEffect(() => {
    // se utiliza cuando se le da generar pago desde la factura
    if (!Loading && DataTable && DataTable.current) {
      DataTable.current.refresh()
      if (Factura) {
        DataTable.current.selectRow(Factura.id)
        setModalGenerarFactura(true)
        setMontoPago(MontoFacturar)
      } else {
        DataTable.current.refresh()
        DataTable.current.clearSelected()
      }
    }
  }, [Loading])

  useEffect(() => {
    if (Ready && !Loading && DataTable) {
      DataTable.current.refresh()
      DataTable.current.clearSelected()
    }
  }, [PersonaRendir])

  useEffect(() => {
    if (!ModalGenerarFactura && !Loading && DataTable) {
      DataTable.current.refresh()
      DataTable.current.clearSelected()
    }
  }, [ModalGenerarFactura])

  useEffect(() => {
    if (Ready) {
      if (FilasSeleccionadas.length > 0 && PersonaRendirId !== null) {
        setHabilitarGenerarFactura(true)
        let monto = 0
        FilasSeleccionadas.map((row) => {
          monto += row.pendiente
        })
        setMontoFacturar(monto)
        setMontoPago(monto)
      } else {
        setHabilitarGenerarFactura(false)
        setMontoFacturar(0)
        setMontoPago(0)
      }
    }
  }, [FilasSeleccionadas, PersonaRendirId])

  useEffect(() => {
    if (Factura === null) {
      if (DataTable && DataTable.current) {
        DataTable.current.clearSelected()
        DataTable.current.selectRow(0)
        DataTable.current.refresh()
      }
    }
  }, [Factura])
  return (
    <Page login_required admin solo_admin>
      <IonAlert
        isOpen={showAlert}
        onDidDismiss={() => setShowAlert(false)}
        header={'Registrar pago'}
        message={'Confirma que desea registrar el pago?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'btn-secondary',
          },
          {
            text: 'Aceptar',
            cssClass: 'btn-primary',
            handler: () => {
              registrarPago()
            },
          },
        ]}
      />
      <IonModal
        isOpen={ModalGenerarFactura}
        backdropDismiss={true}
        onDidDismiss={() => {
          setModalGenerarFactura(false)
          setMontoPago(0)
          setFechaFactura('')
        }}
      >
        <IonHeader>
          <IonToolbar>
            <IonTitle>Registrar pago</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <div className={'mt-4 mx-4'}>
            <IonLabel className="ion-text-wrap">
              Pago a registrar a nombre de <strong>{PersonaRendir && PersonaRendir.label} </strong>con{' '}
              <strong>{FilasSeleccionadas.length} </strong>
              {FilasSeleccionadas.length === 1 ? 'item' : 'items'} por el monto de
              <strong> Gs. {numeral(MontoFacturar).format('0,0')}</strong>
            </IonLabel>
          </div>
          <div className={'mt-4 mx-4 mb-2'}>
            <small>Si desea realizar un pago parcial debe seleccionar solo una factura</small>
          </div>
          <div className={'mx-3 pt-3'}>
            <div className={'pb-3'}>
              <IonLabel>
                <strong>Cuenta de fondos</strong>
              </IonLabel>
            </div>
            <Select
              onChange={(val) => {
                setSelectedFundAccount(val)
                val && setSelectedFundAccount(val.value)
              }}
              placeholder={'Seleccionar cuenta de fondos'}
              options={FundAccounts}
              theme={(theme) => ({
                ...theme,
                colors: {
                  ...theme.colors,
                  primary: '#303AB2',
                  neutral50: 'black',
                },
              })}
            />
          </div>
          <IonList>
            <IonItem>
              <IonLabel style={{ minWidth: 180 }} position="fixed">
                <strong>Monto</strong>
              </IonLabel>
              <input
                className={'form-control'}
                value={'Gs. ' + numeral(MontoPago).format('0,0')}
                onChange={(e) => {
                  let monto = e.target.value
                  monto = monto.replace('Gs. ', '')
                  monto = numeral(monto).value()
                  if (parseInt(monto) > MontoFacturar) {
                    setMontoPago(MontoFacturar)
                  } else {
                    setMontoPago(parseInt(monto))
                  }
                }}
                disabled={FilasSeleccionadas.length > 1}
              />
            </IonItem>
            <IonItem>
              <IonLabel style={{ minWidth: 172 }} position="fixed">
                <strong>Fecha del pago</strong>
              </IonLabel>
              <IonDatetime
                displayFormat="DD/M/YYYY"
                min="2021"
                max="2040"
                value={FechaFactura}
                cancelText={'Cancelar'}
                doneText={'Seleccionar'}
                placeholder={'10/10/2021'}
                onIonChange={(e) => {
                  // @ts-ignore
                  setFechaFactura(moment(e.target.value).format())
                }}
              />
            </IonItem>
          </IonList>
        </IonContent>
        <div style={{ padding: 10 }}>
          <div className={'row'}>
            <div className={'col-12 col-md-6 mb-xs-2'}>
              <button
                className={'btn btn-secondary btn-block'}
                onClick={() => {
                  setModalGenerarFactura(!ModalGenerarFactura)
                  setMontoPago(0)
                  setFechaFactura('')
                }}
              >
                Cancelar
              </button>
            </div>
            <div className={'col-12 col-md-6'}>
              <button
                className={'btn btn-primary btn-block'}
                onClick={() => {
                  setShowAlert(true)
                }}
                disabled={
                  JSON.stringify(MontoPago) === 'null' || FechaFactura === '' || SelectedFundAccount === undefined
                }
              >
                Aceptar
              </button>
            </div>
          </div>
        </div>
      </IonModal>
      <LoaderAdmin visible={Loading} />
      {!Loading && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h2 className="text-themecolor">Registrar pago proveedores</h2>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">
                <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/')}>Inicio</a>
                  </li>
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push(RUTAS.ADMIN_PAGO_PROVEEDOR)}>Pagos proveedor</a>
                  </li>
                  <li className="breadcrumb-item active">Registrar pago proveedores</li>
                </ol>
              </div>
            </div>
            <div className="card mb-0">
              <div className="card-body">
                <div className={'row'}>
                  <div className={'col-4'}>
                    <h4 className={'mb-4'}>Tipo</h4>
                    <Select
                      value={Tipo}
                      onChange={(val) => {
                        setTipo(val)
                        val && setTipoId(val.value)
                      }}
                      options={[
                        { value: 10, label: 'Proveedor' },
                        { value: 20, label: 'Driver' },
                      ]}
                      theme={(theme) => ({
                        ...theme,
                        colors: {
                          ...theme.colors,
                          primary: '#303AB2',
                          neutral50: 'black',
                        },
                      })}
                    />
                  </div>
                  <div className={'col-4'}>
                    <h4 className={'mb-4'}>Persona a rendir</h4>
                    <Select
                      value={PersonaRendir}
                      onChange={(val) => {
                        setPersonaRendir(val)
                        val ? setPersonaRendirId(val.value) : setPersonaRendirId(null)
                      }}
                      options={Data}
                      isSearchable
                      placeholder={'Seleccionar persona a rendir'}
                      isClearable
                      theme={(theme) => ({
                        ...theme,
                        colors: {
                          ...theme.colors,
                          primary: '#303AB2',
                          neutral50: 'black',
                        },
                      })}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className={'row'}>
              <div className={'col-8'}>
                <h2 className="text-themecolor my-4">Seleccionar facturas pendientes</h2>
              </div>
              <div className={'col-4'}>
                <div className={'text-right mt-3'}>
                  <div className={'btn-group ml-auto'}>
                    <div className={'text-right'}>
                      <button
                        className={'btn btn-primary'}
                        disabled={!HabilitarGenerarFactura}
                        title={'Registrar pago'}
                        onClick={() => {
                          setModalGenerarFactura(true)
                          setMontoPago(MontoFacturar)
                        }}
                      >
                        <FIcon.FileText size={14} style={{ marginBottom: 3 }} /> Registrar pago
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="card mb-0">
              <div className="card-body">
                <SDataTable
                  ref={DataTable}
                  subHeader
                  selectableRows
                  selectedRows={setFilasSeleccionadas}
                  obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                    return rootStore.environment.api.pagoProveedoresStaff(
                      page,
                      perPage,
                      order,
                      search,
                      TipoId,
                      PersonaRendirId,
                    )
                  }}
                  columns={[
                    {
                      selector: 'id',
                      omit: true,
                      cell: (row: any) => row.id,
                    },
                    {
                      name: 'Fecha',
                      selector: 'fecha',
                      sortable: false,
                      cell: (row: any) => moment(row.fecha).format('DD/MM/YYYY'),
                    },
                    {
                      name: 'N. factura',
                      selector: 'descripcion',
                      sortable: false,
                      cell: (row: any) => row.descripcion,
                    },
                    {
                      name: 'Razón social',
                      selector: 'nombre',
                      sortable: false,
                      cell: (row: any) => row.nombre,
                    },
                    {
                      name: 'Total',
                      selector: 'monto',
                      sortable: false,
                      cell: (row: any) => {
                        return 'Gs. ' + numeral(row.monto).format('0,0')
                      },
                    },
                    {
                      name: 'Saldo',
                      selector: 'pendiente',
                      sortable: false,
                      cell: (row: any) => {
                        return 'Gs. ' + numeral(row.pendiente).format('0,0')
                      },
                    },
                  ]}
                />
              </div>
            </div>
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminPagoCreate
